import React from 'react';
import styled from "styled-components";

import MapPoint from '../img/map-point.svg';

const TitleContainer = styled.div`
    height: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 32px 64px 32px 64px;
`;

const Header = styled.div`
    display: flex;
    justify-content: space-between;
`;
const HeaderTitle = styled.p`
    line-height: 1;
    margin: 0;
    font-weight: bold;
    font-size: 30px;
    color: #0EC261;
`;
const HeaderPlace = styled.p`
    display: flex;
    margin: 0;
    align-self: center;
    font-weight: normal;
    font-size: 14px;
    color: #999999;
`;
const Img = styled.img`
    margin-right: 8px;
`;

const Main = styled.div``;

const MainContent = styled.div`
    font-weight: bold;
    font-size: 70px;
    margin-bottom: 34px;
`;
const First = styled.p`
    margin: 0;
    line-height: 1;
    color: #121212;
`;
const Second = styled.p`
    margin: 0;
    line-height: 1;
    color: #0EC261;
`;
const SecondTitle = styled.div`
    font-weight: 300;
    font-size: 26px;
    color: #999999;
    margin-bottom: 60px;
`;
const Button = styled.button`
    cursor: pointer;
    line-height: 1;
    font-weight: 500;
    font-size: 18px;
    color: #FFFFFF;
    
    padding: 15px 60px 15px 60px;
    
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
    border: none;
    border-radius: 8px;
    
    transition: background 5s ease-out;
    
    &:hover {
       background: linear-gradient(90deg, #0B934A 2.61%, #026E47 112.6%); 
    }
    
    &:active {
        background: linear-gradient(90deg, #076432 2.61%, #013C27 112.6%);
    }
`;

const Footer = styled.div`
    display: flex;
    justify-content: space-between;
`;
const Copyright = styled.div`
    font-weight: normal;
    font-size: 13px;
    color: #999999;
`;
const Phone = styled.div`
    font-weight: normal;
    font-size: 13px;
    color: #121212;
`;

export const MainTitle = () => {
    return (
        <TitleContainer>
            <Header>
                <HeaderTitle>
                    Need for drive
                </HeaderTitle>
                <HeaderPlace>
                    <Img src={MapPoint}/>
                    Ульяновск
                </HeaderPlace>
            </Header>
            <Main>
                <MainContent>
                    <First>Каршеринг</First>
                    <Second>Need for drive</Second>
                </MainContent>
                <SecondTitle>Поминутная аренда авто твоего города</SecondTitle>
                <Button>Забронировать</Button>
            </Main>
            <Footer>
                <Copyright>
                    © 2016-2019 «Need for drive»
                </Copyright>
                <Phone>
                    8 (495) 234-22-44
                </Phone>
            </Footer>
        </TitleContainer>
    );
};
