import React from 'react';
import styled from "styled-components";

import { MainMenu } from "./Components/MainMenu";
import { MainTitle } from "./Components/MainTitle";
import { MainSlider } from "./Components/MainSlider";
import { MainMobile } from "./Components/MainMobile";
import { MainModalMenu } from "./Components/MainModalMenu";
import { MainModalMenuMobile } from "./Components/MainModalMenuMobile";

export const MainPage = () => {

    const [openItem, setOpenItem] = React.useState('none');

    const Container = styled.div`
        display: flex;
        height: 100vh;
        
        @media(max-width: 560px) {
            display: none;
        }
    `;

    const ContainerMobile = styled.div`
        height: 100vh;
        display: none;
        
        @media(max-width: 560px) {
            display: flex;
        }
    `;

    const MainMenuContainer = styled.section`
        width: 4%;
        height: 100%;
        background: #151B1F;;
         
        @media(max-width: 819px) {
            width: 6%; 
        }
    `;

    const MainTitleContainer = styled.section`
        width: 48%;
        height: 100%;
        
        @media(max-width: 1084px) {
            width: 58%; 
        }
        @media(max-width: 819px) {
            width: 100%; 
        }
    `;

    const MainSliderContainer = styled.section`
        width: 48%;
        height: 100%;
        
        @media(max-width: 1084px) {
            text-align: center;
            width: 38%; 
        }
        @media(max-width: 819px) {
            display: none;
        }
    `;

    return (
        <React.Fragment>
            <Container>
                <MainModalMenu openItem={openItem} setOpenItem={setOpenItem}/>
                <MainMenuContainer>
                    <MainMenu setOpenItem={setOpenItem}/>
                </MainMenuContainer>
                <MainTitleContainer>
                    <MainTitle/>
                </MainTitleContainer>
                <MainSliderContainer>
                    <MainSlider/>
                </MainSliderContainer>
            </Container>
            <ContainerMobile>
                <MainMobile openItem={openItem} setOpenItem={setOpenItem}/>
                <MainModalMenuMobile openItem={openItem} setOpenItem={setOpenItem}/>
            </ContainerMobile>
        </React.Fragment>
    );
}
