import React from 'react';
import styled from "styled-components";

import MenuBurgerImg from '../img/menu_burger_mobile.svg';
import MapPoint from '../img/map-point.svg';

const MobileContainer = styled.div`
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;                     
`;

const Header = styled.div`
    margin: 16px 16px 0 16px;
`;
const FirstHeaderRow = styled.div`
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
`;
const SecondHeaderRow = styled.div`
    display: flex;
    justify-content: flex-end;
`;
const HeaderPlace = styled.div`
    font-weight: normal;
    font-size: 14px;
    color: #999999;
`;
const Img = styled.img`
    margin-right: 8px;
`;
const HeaderTitle = styled.p`
    margin: 0;
    font-weight: bold;
    font-size: 30px;
    color: #0EC261;
`;

const Main = styled.div`
    padding: 0 16px 0 16px;
`;
export const MainText = (props) => {
    const Text = styled.p`
        font-weight: bold;
        font-size: 32px;
        margin-bottom: ${props.margin}px;
        line-height: 1;
        color: #${props.color};
    `;

    return <Text>{props.text}</Text>
}
const MainSecond = styled.p`
    margin-bottom: 32px;
    font-weight: 300;
    font-size: 18px;
    color: #999999;
`;
const Button = styled.button`
    cursor: pointer;
    line-height: 1;
    font-weight: 500;
    font-size: 18px;
    color: #FFFFFF;
    
    padding: 15px 60px 15px 60px;
    
    background: linear-gradient(90deg, #0EC261 2.61%, #039F67 112.6%);
    border: none;
    border-radius: 8px;
    
    transition: background 5s ease-out;
    
    &:active {
        background: linear-gradient(90deg, #076432 2.61%, #013C27 112.6%);
    }
`;

const Footer = styled.div`
    background: #151B1F;
    text-align: right;
    padding: 16px;
`;


export const FooterText = (props) => {
    const Text = styled.p`
        font-size: 13px;
        color: #${props.color};
        margin-bottom: 8px;
    `;
    return <Text>{props.text}</Text>
}

export const MainMobile = ({setOpenItem}) => {

    const openMenu = () => {
        setOpenItem('flex');
    }
    return (
        <MobileContainer>
            <Header>
                <FirstHeaderRow>
                    <img src={MenuBurgerImg} onClick={openMenu}/>
                    <HeaderTitle>Need for drive</HeaderTitle>
                </FirstHeaderRow>
                <SecondHeaderRow>
                    <Img src={MapPoint}/>
                    <HeaderPlace>Ульяновск</HeaderPlace>
                </SecondHeaderRow>
            </Header>
            <Main>
                <MainText margin='6' color='121212' text='Каршеринг'/>
                <MainText margin='16' color='0EC261' text='Need for drive'/>
                <MainSecond>Поминутная аренда авто твоего города</MainSecond>
                <Button>Забронировать</Button>
            </Main>
            <Footer>
                <FooterText text='8 (495) 234-22-44' color='0EC261'/>
                <FooterText text='© 2016-2019 «Need for drive»' color='EEEEEE'/>
            </Footer>
        </MobileContainer>
    )
};
