import React from 'react';
import styled from "styled-components";

import MenuBurgerImg from '../img/menu_burger.svg';
import MenuBurgerImgHover from '../img/menu_btn_hover.svg'

const MenuContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    align-items: center;
`;

const MenuBurger = styled.div`
    height: 32px;
    width: 32px;
    cursor: pointer;
    margin-top: 32px;
    background-image: url(${MenuBurgerImg});
    
    transition: background-image .5s ease;
    
    &:hover {
        background-image: url(${MenuBurgerImgHover});
    }
`;

const MenuLangContainer = styled.div`
    display: flex;
    justify-content: center;
    
    cursor: pointer;
    height: 48px;
    width: 48px;
    margin-bottom: 19px;
    
    align-items: center;
    align-self: center;
    
    transition: border .5s ease;
    
    
    &:hover {
        margin-bottom: 19px;
        border: 1px solid #EEEEEE;
        border-radius: 50%;
        
        @media(max-width: 1084px) 
            {
                border: none;
            }
    }
    
    &:hover .menu-lang {
        color: #EEEEEE
    }
    
    &:active .menu-lang {
        transition: none;
        color: #0EC261;
    }
    
    
`;

const MenuLang = styled.span`
    line-height: 1;
    font-weight: bold;
    text-align: center;
    font-size: 13px;
    color: #0EC261;
    
    transition: color .5s ease;
`;

export const MainMenu = ({setOpenItem}) => {

    const openMenu = () => {
        setOpenItem('flex');
    }

    return (
        <MenuContainer>
            <MenuBurger onClick={openMenu}/>
            <MenuLangContainer>
                <MenuLang className='menu-lang'>
                    Eng
                </MenuLang>
            </MenuLangContainer>
        </MenuContainer>
    )
}
